/**       */
export const findPathDFS = async (
  graph: number[][],
  start: number[],
  end: number[],
  startValue: number
) => {
  const directions = getImportance(start, end);
  const timer = setTimeout(() => {
    return null;
  }, 3000);
  if (start[0] === end[0] && start[1] === end[1]) return null;
  let bend = 0;
  const history: number[][] = [];

  const path = await dfs(graph, start, end, history, directions, startValue);

  clearTimeout(timer);

  return path;
};

/**   direction    */
const getImportance = (start: number[], end: number[]) => {
  const [sx, sy] = start;
  const [ex, ey] = end;
  let directions = [] as number[][];

  if (sx > ex && sy > ey) {
    // 30,15 15,13
    //
    directions = [
      [0, -1], // 
      [-1, 0], // 
      [0, 1], // 
      [1, 0], // 
    ];
    // console.log("");
  } else if (sx > ex && sy < ey) {
    directions = [
      [0, 1], // 
      [-1, 0], // 
      [0, -1], // 
      [1, 0], // 
    ];
    // console.log("");
  } else if (sx > ex && sy === ey) {
    directions = [
      [-1, 0], // 
      [0, -1], // 
      [0, 1], // 
      [1, 0], // 
    ];
    // console.log("");
  } else if (sx < ex && sy > ey) {
    directions = [
      [0, -1], // 
      [1, 0], // 
      [-1, 0], // 
      [0, 1], // 
    ];
    // console.log("");
  } else if (sx < ex && sy < ey) {
    directions = [
      [0, 1], // 
      [1, 0], // 
      [-1, 0], // 
      [0, -1], // 
    ];
    // console.log("");
  } else if (sx < ex && sy === ey) {
    directions = [
      [1, 0], // 
      [0, -1], // 
      [0, 1], // 
      [-1, 0], // 
    ];
    // console.log("");
  } else if (sx === ex && sy > ey) {
    directions = [
      [0, -1], // 
      [-1, 0], // 
      [1, 0], // 
      [0, 1], // 
    ];
    // console.log("");
  } else if (sx === ex && sy < ey) {
    directions = [
      [0, 1], // 
      [-1, 0], // 
      [1, 0], // 
      [0, -1], // 
    ];
    // console.log("");
  } else {
    directions = [
      [-1, 0], // 
      [1, 0], // 
      [0, -1], // 
      [0, 1], // 
    ];
    // console.log("");
  }
  return directions;
};

/**    */
export const createBoard = (rows: number, cols: number) => {
  rows = rows + 2;
  cols = cols + 2;
  // 0  rows x cols  2  
  const board = Array.from({ length: rows }, () => Array(cols).fill(0));

  const maxItem = (rows - 2) * (cols - 2);
  if (maxItem % 2 !== 0) return [[]]; //   
  let cnt = 1;
  const itemLength: number[] = [1, 2, 3];
  if (cols > 14) itemLength.push(4, 5, 6, 7, 8, 9);
  // const itemLength: number[] = [1];
  while (cnt < 10) {
    // cnt++;
    cnt++;

    // 12 / 6    
    if (
      Number.isInteger(maxItem / itemLength.length) &&
      (maxItem / itemLength.length) % 2 === 0
    ) {
      break;
    }
    itemLength.pop();

    // itemLength.push(cnt);
  }

  const maxStack = maxItem / itemLength.length; // 12  3 12/ = 4,  4

  const itemStack = Array.from({ length: itemLength.length }, (i) =>
    Array(maxStack).fill(1)
  ); // [[],[],[]]
  console.log(Math.floor(Math.random() * itemLength.length));
  for (let i = 0; i < board.length; i++) {
    if (i === 0 || i === board.length - 1) continue;
    for (let r = 1; r < cols - 1; r++) {
      const randomNumber = Math.floor(Math.random() * itemLength.length); // 

      if (itemStack[randomNumber].length > 0) {
        itemStack[randomNumber].pop();
        board[i][r] = randomNumber + 1;
      } else {
        r--;
      }
    }
  }

  return board;
  const zz = [
    [0, 0, 0, 0, 0],
    [0, 1, 2, 3, 0],
    [0, 3, 1, 2, 0],
    [0, 1, 1, 2, 0],
    [0, 3, 3, 2, 0],
    [0, 0, 0, 0, 0],
  ];
  return zz;
};

//     
const calculTransXY = (pathArr: number[][], underBand: number) => {
  let bend = 0;
  const arrXY = [];

  // console.log(pathArr);
  //     ["r","c","c","r","c"]
  for (let i = 1; i < pathArr.length; i++) {
    arrXY.push(
      pathArr[i][0] === pathArr[i - 1][0] && pathArr[i][1] !== pathArr[i - 1][1]
        ? "col"
        : "row"
    );
  }
  //  
  for (let i = 1; i < arrXY.length; i++) {
    if (arrXY[i] !== arrXY[i - 1]) {
      bend++;
    }
  }

  if (bend >= underBand) return false;
  return true;
};

export const playerBot = async (board: number[][]): Promise<number[][]> => {
  // const graph = JSON.parse(JSON.stringify(board));
  const graph = [...board];
  if (graph.length === 0 || graph == null) return [[]];
  const isPath = await remainingPathFinder(graph, "bot");
  console.log(isPath, graph);
  if (isPath === undefined) {
    throw new Error("404");
  }
  return graph;
};

const dfs = async (
  graph: number[][],
  start: number[],
  end: number[],
  path: number[][],
  directions: number[][],
  startValue: number
): Promise<number[][] | null> => {
  const [x, y] = start;
  path.push(start);
  // visitPath[x][y] = true;
  const isUnder3Band = calculTransXY(path, 3);
  if (!isUnder3Band) {
    // console.log(" 3,    .");
    return null;
  }
  // console.log("History: ", path, "End", end);
  // console.log(directions);
  //     
  if (x === end[0] && y === end[1] && graph[x][y] === startValue) {
    return path;
  }

  //           .
  //    for      
  for (const [dx, dy] of directions) {
    const newX = x + dx;
    const newY = y + dy;
    if (newX === end[0] && newY === end[1]) {
      //   
      const foundPath = await dfs(
        graph,
        [newX, newY],
        end,
        path.slice(),
        directions,
        startValue
      );
      if (foundPath) {
        // const success = calculTransXY(foundPath, 3);
        return foundPath;
        // return success ? foundPath : null;
      } else {
        return null;
      }
    }
  }

  //      
  for (const [dx, dy] of directions) {
    const newX = x + dx;
    const newY = y + dy;
    const exists = path.some(([x, y]) => x === newX && y === newY); //     false
    if (
      newX >= 0 && //  
      newX < graph.length && //   
      newY >= 0 &&
      newY < graph[0].length &&
      !exists && //   
      graph[newX][newY] === 0 //   0 
    ) {
      const foundPath = await dfs(
        graph,
        [newX, newY],
        end,
        path.slice(),
        directions,
        startValue
      );

      if (foundPath) {
        return foundPath;
        // const success = calculTransXY(foundPath, 3);
        // return success ? foundPath : null;
      }
    }
  }

  return null; //     null 
};

