export const getAiInt = (aiInt: string | undefined): number => {
  if (aiInt === "" || aiInt === "FOOL") {
    return 5;
  }
  if (aiInt === "" || aiInt === "AVG") {
    return 3;
  }
  if (aiInt === "" || aiInt === "Univ") {
    return 1;
  }
  if (aiInt === "" || aiInt === "GOD") {
    return 0;
  }
  return 5;
};
